body {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
    background-image: url('/public/images/bg.jpg'), linear-gradient(45deg, #ff9a9e 0%, #fad0c4 99%, #fad0c4 100%);
    background-size: cover, 200% 200%;
    background-attachment: fixed;
    background-position: center;
    color: #333;
    animation: gradientBG 15s ease infinite;
}

@keyframes gradientBG {
    0% {background-position: 0% 50%;}
    50% {background-position: 100% 50%;}
    100% {background-position: 0% 50%;}
}

.App {
    text-align: center;
}

.App-header {
    background-color: rgba(40, 44, 52, 0.7);
    padding: 20px;
    color: #fff;
    box-shadow: 0 4px 6px rgba(0,0,0,0.1);
    backdrop-filter: blur(5px);
}

.simulator {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 20px;
    position: relative;
    z-index: 1;
}

.simulator-column {
    width: 45%;
    min-width: 300px;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 0 10px 20px rgba(0,0,0,0.1);
    margin: 10px;
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
    position: relative;
    overflow: hidden;
}

.simulator-column::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: linear-gradient(
        to bottom right,
        rgba(255,255,255,0.2) 0%,
        rgba(255,255,255,0.05) 40%,
        rgba(255,255,255,0) 50%
    );
    transform: rotate(45deg);
    pointer-events: none;
}

.simulator-column:hover {
    transform: translateY(-5px);
    box-shadow: 0 15px 30px rgba(0,0,0,0.2);
}

/* Transmitter styling */
.simulator-column:nth-child(1) {
    background-color: rgba(100, 149, 237, 0.3);
    border: 2px solid rgba(100, 149, 237, 0.7);
}

/* Receiver styling */
.simulator-column:nth-child(2) {
    background-color: rgba(144, 238, 144, 0.3);
    border: 2px solid rgba(144, 238, 144, 0.7);
}

.control-panel {
    margin-bottom: 20px;
}

.control-btn {
    padding: 12px 24px;
    margin: 0 10px;
    font-size: 16px;
    cursor: pointer;
    border: none;
    border-radius: 50px;
    transition: all 0.3s;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 1px;
}

.start {
    background-color: rgba(76, 175, 80, 0.8);
    color: white;
}

.start:hover {
    background-color: rgba(76, 175, 80, 1);
    box-shadow: 0 5px 15px rgba(76, 175, 80, 0.4);
}

.stop {
    background-color: rgba(244, 67, 54, 0.8);
    color: white;
}

.stop:hover {
    background-color: rgba(244, 67, 54, 1);
    box-shadow: 0 5px 15px rgba(244, 67, 54, 0.4);
}

.disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.log-display {
    height: 300px;
    overflow-y: auto;
    border: 1px solid rgba(221, 221, 221, 0.5);
    padding: 10px;
    text-align: left;
    font-family: 'Courier New', monospace;
    background-color: rgba(249, 249, 249, 0.3);
    color: #333;
    border-radius: 10px;
}

.log-entry {
    margin: 5px 0;
    padding: 5px;
    border-bottom: 1px solid rgba(238, 238, 238, 0.5);
    transition: background-color 0.3s;
}

.log-entry:hover {
    background-color: rgba(240, 240, 240, 0.5);
}

.clear-logs-btn {
    margin-top: 20px;
    padding: 12px 24px;
    font-size: 16px;
    cursor: pointer;
    background-color: rgba(33, 150, 243, 0.8);
    color: white;
    border: none;
    border-radius: 50px;
    transition: all 0.3s;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 1px;
}

.clear-logs-btn:hover {
    background-color: rgba(33, 150, 243, 1);
    box-shadow: 0 5px 15px rgba(33, 150, 243, 0.4);
    transform: translateY(-2px);
}

.encoding-selection {
    background-color: rgba(255, 255, 255, 0.2);
    padding: 20px;
    border-radius: 15px;
    margin: 20px 0;
    box-shadow: 0 10px 20px rgba(0,0,0,0.1);
    backdrop-filter: blur(5px);
    width: 100%;
    max-width: 800px;
}

.encoding-selection h3 {
    margin-bottom: 15px;
    color: #333;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.btn-group {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.btn-group button {
    background-color: rgba(0, 123, 255, 0.7);
    border: none;
    color: white;
    padding: 12px 24px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px;
    cursor: pointer;
    border-radius: 50px;
    transition: all 0.3s;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 1px;
}

.btn-group button:hover {
    background-color: rgba(0, 123, 255, 1);
    box-shadow: 0 5px 15px rgba(0, 123, 255, 0.4);
    transform: translateY(-2px);
}

.btn-group button.active {
    background-color: rgba(76, 175, 80, 0.8);
}

.btn-group button.active:hover {
    background-color: rgba(76, 175, 80, 1);
    box-shadow: 0 5px 15px rgba(76, 175, 80, 0.4);
}

/* Scrollbar styling */
.log-display::-webkit-scrollbar {
    width: 8px;
}

.log-display::-webkit-scrollbar-track {
    background: rgba(241, 241, 241, 0.3);
    border-radius: 10px;
}

.log-display::-webkit-scrollbar-thumb {
    background: rgba(136, 136, 136, 0.5);
    border-radius: 10px;
}

.log-display::-webkit-scrollbar-thumb:hover {
    background: rgba(85, 85, 85, 0.7);
}

@media (max-width: 1023px) {
    .simulator-column {
        width: 90%;
    }
}
